@import "../../Styles/Variables/Variables.scss";

#nav {
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  margin: 10rem 100rem 0;
  z-index: 999;

  @media (max-width: 550px) {
    justify-content: center;
  }

  .logo_container {
    width: 70rem;
    height: 71rem;

    .logo {
      min-width: 100%;
    }
  }

  .links_container {
    display: flex;
    align-items: center;
    margin-left: auto;
    column-gap: 20rem;
    list-style-type: none;

    @media (max-width: 550px) {
      display: none;
    }

    .link {
      font-size: 16rem;
      color: #fff;
      font-weight: 100;
      cursor: pointer;
    }
  }
}
