@import "../../Styles/Variables/Variables.scss";

#hero {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(
    0deg,
    rgba(165, 249, 248, 1) 0%,
    rgba(160, 146, 255, 1) 90%
  );
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;

  .container {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    margin: 100rem;
    width: 100%;
    row-gap: 20rem;
    z-index: 2;

    @media (min-width: 768px) {
      grid-template-columns: 1fr 1fr;
    }

    .heading_container {
      display: flex;
      flex-direction: column;
      align-items: center;
      row-gap: 50rem;
      width: 300rem;
      text-align: center;

      @media (min-width: 768px) {
        text-align: left;
        align-items: flex-start;
      }

      @media (min-width: 1024px) {
        width: 511rem;
      }

      .main_heading {
        font-size: 36rem;
        font-weight: 300;
        color: #6c59ea;

        .text_color {
          color: #5ffffd;
          font-size: 36rem;
          font-weight: 300;
        }

        @media (min-width: 1024px) {
          font-size: 64rem;

          .text_color {
            font-size: 64rem;
          }
        }
      }

      .paragraph {
        font-size: 16rem;
        font-weight: 100;
        color: #6c59ea;
        max-width: 290rem;

        .text_color {
          color: #5ffffd;
          font-size: 16rem;
          font-weight: 300;
        }

        @media (min-width: 1024px) {
          font-size: 20rem;
          max-width: 380rem;

          .text_color {
            font-size: 20rem;
          }
        }
      }

      .btn_container {
        display: flex;
        flex-direction: column;
        row-gap: 10rem;

        .main_btn {
          font-size: 16rem;
          width: 150rem;
          font-weight: 400;
          border: none;
          border-radius: 20rem;
          background-color: #6c59ea;
          padding: 10rem 0;
          color: #fff;
          cursor: pointer;
        }

        .sec_btn {
          font-size: 16rem;
          width: 150rem;
          font-weight: 400;
          border: none;
          border-radius: 20rem;
          background-color: #5ffffd;
          padding: 10rem 0;
          color: #fff;
          cursor: pointer;
        }
      }
    }
    .image_container {
      position: relative;
      .hero_img {
        width: 185rem;
        height: 549rem;
      }
    }
  }
}
